import React from "react";
import { Redirect } from "react-router-dom";
import ReactAudioPlayer from "react-audio-player";
import { Wizard, WizardStep } from "react-wizard-primitive";
import Lottie from "lottie-react-web";
import Slider from "@material-ui/core/Slider";

import Animation1 from "./json/68.json";
import Animation2 from "./json/69.json";

import textura from "../../styles/textura.png";

class Step8 extends React.Component {
  componentDidMount() {
    window.scrollTo(0, 0);
  }
  state = {
    slider1: 0
  };

  handleChange = name => (e, value) => {
    this.setState({
      [name]: value
    });
  };
  render() {
    const { slider1 } = this.state;

    function map_range(valor, low1, high1, low2, high2) {
      return low2 + ((high2 - low2) * (valor - low1)) / (high1 - low1);
    }
    if (slider1 === 100) {
      return <Redirect to="/subida" />;
    }
    return (
      <div className="size">
        <ReactAudioPlayer
          src={
            "https://raw.githubusercontent.com/romerobananas/sound/master/6.ogg"
          }
          volume={1}
          loop={true}
          autoPlay
        />
        <div>
          <img src={textura} className="textura" alt="textura" />
        </div>
        <Wizard>
          <WizardStep>
            {({ isActive }) =>
              isActive && (
                <div className="size">
                  <div className="sliderdiv">
                    <Slider
                      className="slider"
                      value={slider1}
                      aria-labelledby="label"
                      onChange={this.handleChange("slider1")}
                      max={100}
                      min={0}
                    />
                  </div>
                  <div className="lottie" style={{ opacity: 1 }}>
                    <Lottie
                      direction={1}
                      speed={1}
                      options={{
                        animationData: Animation1,
                        loop: true
                      }}
                    />
                  </div>
                  <div
                    className="lottie"
                    style={{ opacity: map_range(slider1, 0, 75, 0, 1) }}
                  >
                    <Lottie
                      direction={1}
                      speed={1}
                      options={{
                        animationData: Animation2,
                        loop: true
                      }}
                    />
                  </div>
                </div>
              )
            }
          </WizardStep>
        </Wizard>
      </div>
    );
  }
}
export default Step8;
