import React from "react";
import { Redirect } from "react-router-dom";
import ReactAudioPlayer from "react-audio-player";
import { Wizard, WizardStep } from "react-wizard-primitive";
import Lottie from "lottie-react-web";

import Animation1 from "./json/82.json";
import textura from "../../styles/textura.png";

class Step10 extends React.Component {
  componentDidMount() {
    this.startCounting();
  }
  constructor(...args) {
    super(...args);

    this.state = { counter: 0.3, elapsedTime: null };
    this.cincrement = this.cincrement.bind(this);
    this.countUp = this.countUp.bind(this);
    this.startCounting = this.startCounting.bind(this);
  }
  startCounting() {
    setInterval(this.countUp, 1000);
  }
  countUp() {
    this.setState(({ elapsedTime }) => ({ elapsedTime: elapsedTime + 1 }));
  }

  cincrement() {
    this.setState(state => ({ counter: state.counter + 1 }));
  }

  render() {
    const { elapsedTime } = this.state;
    if (elapsedTime === 13) {
      return <Redirect to="/cierre" />;
    }
    return (
      <div className="size">
        <ReactAudioPlayer
          src={
            "https://raw.githubusercontent.com/romerobananas/sound/master/7.ogg"
          }
          volume={1}
          loop={true}
          autoPlay
        />
        <div>
          <img src={textura} className="textura" alt="textura" />
        </div>
        <Wizard>
          <WizardStep>
            {({ isActive }) =>
              isActive && (
                <div className="size">
                  <div className="lottie">
                    <Lottie
                      direction={1}
                      speed={1}
                      options={{
                        animationData: Animation1,
                        loop: true
                      }}
                    />
                  </div>
                </div>
              )
            }
          </WizardStep>
        </Wizard>
      </div>
    );
  }
}
export default Step10;
